.sidecar {
  position: absolute;
  z-index: 10;
  transform: translateX(-100%);
  .title {
    &:before {
      right: 145px; } }
  .hideSidcar {
    background-color: transparent;
    border: none;
    position: relative;
    width: 30px;
    height: 30px;
    &:before {
      content: '';
      position: absolute;
      width: 7px;
      height: 7px;
      display: block;
      border: {
        right: 1px solid #000;
        bottom: 1px solid #000; }
      transform: rotate(-45deg);
      top: 13px; } }

  &__body {
    padding: 1.15em; }

  &__img {
    display: block;
    margin: 20px auto; }

  &__offer {
    text-align: center;
    padding: 20px 0; }

  &__buttons {
    display: flex;
    justify-content: space-around; }

  &__button {
    padding: 1em 3em;
    border-width: 1px;
    border-color: #EF9F3C;
    &__reject {
      background-color: transparent; }
    &__accept {
      color:  #FDFBF7;
      background-color: #EDEDED; } } }

